import TodoItem from './Components/TodoItem';
import Form from './Components/Form';
import {Component} from 'react';
class  App extends Component 
{
  state={
    items:[
      {id:1,name:"Khalid",age:20},
      {id:2,name:"Omar",age:22},
      {id:3,name:"Mustapha",age:25}
    ]
  }

  AddNew=(item) =>{
    item.id=Math.round(Math.random()*10000);
    const items=this.state.items;
    items.push(item);
    this.setState({
      items:items
    })
  }
  
  RemoveItem=(item)=>
  {
    let newitems=this.state.items;
    newitems=newitems.filter(el=>
      {
        return el.id!==item.id;
      })

    this.setState({
      items:newitems
    })
    
  }
  render()
  {
    return (
      <div className="App"> 
          <div className="container">
            <TodoItem items={this.state.items} RemoveItem={this.RemoveItem} />
            <Form AddNew={this.AddNew}/>
          </div>
      </div>
    );
  }
}

export default App;
