const Items=(props)=>
{
    const {items,RemoveItem}=props;
    const length=items.length;
    const listeitems=length?items.map(el=>
        {
            return(<div className="item" key={el.id}>
                <label className="lblname">{el.name}</label>
                <label className="lblage">{el.age}</label>
                <label className="lblaction" onClick={()=>RemoveItem(el)}><i className="fa fa-times" aria-hidden="true"></i></label>
            </div>)
        }):<p>Add New Itams To Liste</p>;
    return (
        <div className="items">
            <header>
                <label className="lblname">Nom</label>
                <label className="lblage">Age</label>
                <label className="lblaction">Remove</label>
            </header>
            {listeitems}
        </div>
    )
}
export default Items;