import {Component} from "react";
class Form extends Component
{

    state={
        id:"",
        name:"",
        age:""
    }
    handelChange=(e)=>
    {
        this.setState({
            [e.target.name]:e.target.value
        })
    }
    handelSubmit=(e)=>
    {
        e.preventDefault();
        if(this.state.name!=="" && this.state.age!=="")
        { 
            const {AddNew}=this.props;
            AddNew(this.state);
            this.setState({
                    id:"",
                    name:"",
                    age:""
            })
        }
        else
        {
            return false;
        }
    }
    render()
    {
        
        return(
            <div className="form">
                <form  method="post" onSubmit={this.handelSubmit}>
                    <input type="text" name="name" onChange={this.handelChange} value={this.state.name}/>
                    <input type="number" name="age" onChange={this.handelChange} value={this.state.age}/>
                    <input type="submit" value="Add" />
                </form>
            </div>
        )
    }
}
export default Form;